









import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';
import { HttpService } from 'src/services/http/http.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { DeleteService } from 'src/services/delete.service';
import { IUser } from 'src/interfaces/i.user.interface';

@Component({
  selector: 'app-Recover-freight',
  templateUrl: './recover.user.deleted.component.html',
  styles: [
  ]
})
export class RecoverUserDeletedComponent extends SharedComponents implements OnInit {

  parentPath: string;
  constructor(public service: DeleteService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.parentPath = this.activateRouter.parent.routeConfig.path;

    this.service.initRecoverForm()
    this.service.form.controls._id.setValue(this.activateRouter.snapshot.params.targetId);

    //Check If Must Be Get Object From The Server
    if (history.state.name) {
      this.service.form.controls.name.setValue(history.state.name);
      history.state.name = null;
    }
    else {
      //Get From The Serer
      this.getFreightToRecover();
    }
  }

  /**
   * Get Car Mark From Server To Update
   */
  getFreightToRecover() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.getDeletedUserToRecover<IUser>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.form.controls.name.setValue(res.result.name);
          } else
            AlertService.showResponseMessage(res);

        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Recover Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.recoverUser<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.service.onRecoveredItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.service.closeModel(this.parentPath);
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
