import { environment } from 'src/environments/environment';
import { Component, OnInit } from '@angular/core';
import { FOPModel } from 'src/models/fop/fop.model';
import { AlertService } from 'src/services/alert.service';
import { IUser } from 'src/interfaces/i.user.interface';
import { DeleteService } from 'src/services/delete.service';
import { SharedComponents } from 'src/app/shared.components';

@Component({
  selector: 'app-users-deleted',
  templateUrl: './users.deleted.component.html',
  styles: [
  ]
})
export class UsersDeletedComponent extends SharedComponents implements OnInit {

  skip: number = 0;
  limit: number = 30;
  entries: IUser[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
   env = environment;
  constructor(public service: DeleteService) { super(); }

  ngOnInit(): void {
    this.service.seo.updateSEO("usersDeleted");
    this.service.initUsersFilterForm();

    //Get My Users Now
    this.getUsers();

    //On Deleted User
    this.allSubscriptions.push(
      this.service.onRecoveredItem.subscribe(userDeletedId => {
        this.entries.splice(this.entries.findIndex(c => c._id == userDeletedId), 1)
      })
    )
  }

  /**
   * Get My Users
   */
  getUsers(isFilter: boolean = false): void {
    this.fop.paging.isRequestingToServer = true;
    this.service.filterForm.disable();
    if (isFilter) {
      this.entries = [];
      this.skip = 0;
      this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
    }

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getUsers<IUser[]>(this.skip, this.limit, this.service.filterForm.value).subscribe(res => {
        //Enable Filter Form Now
        this.service.filterForm.enable();
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;


        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
        this.fop.paging.isRequestingToServer = false;
        this.service.filterForm.enable();
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
        this.service.filterForm.enable();
      },
        () => {

        }));
  }


  funTrack(index: number, user: IUser): string | undefined {
    return user?._id;
  }

  /**Reset Form Filter And Get Users Affter Reset */
  resetFilter(): void {
    this.service.filterForm.reset();
    this.getUsers(true);
  }




}//End Class
