import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { FinancialTransfersDeletedComponent } from "src/app/back-end/deletes/financial-transfers-deleted/financial.transfers.deleted.component";
import { FreightsDeletedComponent } from "src/app/back-end/deletes/freights-deleted/freights.deleted.component";
import { RecoverTransactionDeletedComponent } from "src/app/back-end/deletes/recover-transaction-deleted/recover.transaction.deleted.component";
import { RecoverUserDeletedComponent } from "src/app/back-end/deletes/recover-user-deleted/recover.user.deleted.component";
import { SectorSalesDeletedComponent } from "src/app/back-end/deletes/sector-sales-deleted/sector.sales.deleted.component";
import { UsersDeletedComponent } from "src/app/back-end/deletes/users-deleted/users.deleted.component";

 
const ROUTS: Routes = [
    {
        path: 'freights', component: FreightsDeletedComponent,
        children: [
            { path: 'recover/:targetId', component: RecoverTransactionDeletedComponent }
        ]
    },
    {
        path: 'sectorSales', component: SectorSalesDeletedComponent,
        children: [
            { path: 'recover/:targetId', component: RecoverTransactionDeletedComponent }
        ]
    },
    {
        path: 'financialTransfers', component: FinancialTransfersDeletedComponent,
        children: [
            { path: 'recover/:targetId', component: RecoverTransactionDeletedComponent }
        ]
    },
    {
        path: 'users', component: UsersDeletedComponent,
        children: [
            { path: 'recover/:targetId', component: RecoverUserDeletedComponent }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class DeletesRoutingModule {

}

