import { Component, OnInit } from "@angular/core";
import { SharedComponents } from "src/app/shared.components";
import { ISectorSale } from "src/interfaces/i.sector.sale.interface";
import { FOPModel } from "src/models/fop/fop.model";
import { AlertService } from "src/services/alert.service";
import { DeleteService } from "src/services/delete.service";


@Component({
  selector: 'app-sector-sale-deleted',
  templateUrl: './sector.sales.deleted.component.html',
  styles: [
  ]
})
export class SectorSalesDeletedComponent extends SharedComponents implements OnInit {
  /**
   * Page Target
   */
  skip: number = 0;
  limit: number = 50;
  entries: ISectorSale[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  formSubmitError: boolean;

  constructor( public service: DeleteService) { super() }

  ngOnInit(): void {
    this.service.seo.updateSEO("sectorSalesDeleted");
    this.getEntiries(false);
    this.service.initSectorSalesFilterForm();

    //Listener On Delete Item Event
    this.allSubscriptions.push(
      this.service.onRecoveredItem.subscribe(deletedItemId => {
        let deletedIndex = this.entries.findIndex(ent => ent._id == deletedItemId);
        if (deletedIndex >= 0) {
          this.entries.splice(deletedIndex, 1);
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
        }
      }));
  }


  /**
 * Get My Users
 */
  getEntiries(isReset): void {

    if (isReset) {
      this.skip = 0;
      this.entries = [];
    }
    this.formSubmitError = false;
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getFreightsDeleted<ISectorSale[]>(this.skip, this.limit, this.service.filterForm.value).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: ISectorSale): string | undefined {
    return entitiy?._id;
  }
}//End CLass
