import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { TransactionTypeEnum } from 'src/enums/tranaction.type.enum';
import { freightCasesArray, freightCasesContainerArray } from '../consts/freight.cases.const';
import { AlertService } from './alert.service';
import { EventEmitter, Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { ICity } from "src/interfaces/i.city.interface";
import { IGeneralSetting } from "src/interfaces/i.general.setting.interface";
import { IState } from "src/interfaces/i.state.interface";
import { IUser } from "src/interfaces/i.user.interface";
import { carSizesArray, carSizesObject } from "src/consts/car.sizes.const";
import { HttpService } from './http/http.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CarSizesEnum } from 'src/enums/car.sizes.enum';
import { FileDragHandle } from 'src/interfaces/file.drag.handel.interface';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { IPort } from 'src/interfaces/i.port.interface';
import { SEOService } from './seo.service';
import { CustomValidatorsService } from './custom.validators.service';
import { BranchesEnum } from 'src/enums/branches.enum';
import { BranchesArray } from 'src/consts/branches.const';

@Injectable()
export class DeleteService {
  clinets: IUser[] = [];
  freightCases = freightCasesArray;

  freightCasesToChange = freightCasesContainerArray;
  buySources: IGeneralSetting[];
  states: IState[] = [];
  allStates: IState[] = [];
  cities: ICity[] = [];
  freightPorts: IPort[] = [];
  arrivalPorts: IPort[] = [];
  carSizes = carSizesArray;
  navigationalWayes: IGeneralSetting[];
  isFormSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  carPicture = {} as FileDragHandle;
  defaultImageUrl: string = "/assets/img/car_default_img.jpg";
  onRecoveredItem: EventEmitter<string> = new EventEmitter<string>();
  filterForm = new FormGroup({});
  form = new FormGroup({});
  freightsSelected: ITransaction[] = [];
  isAllowEnterClinetManually: boolean;
  branches=BranchesArray;
  constructor(public u: CurrentUserService, private router: Router, public http: HttpService, public seo: SEOService) { }
  /**
   * Close: Back To The Page
   */
  public closeModel(backTo: string) {

    this.router.navigate(['/deletes', backTo]);
  }


  /** Init Form For Search In Freights*/
  initFreightsFilterForm() {
    this.filterForm = new FormGroup({
      clinetName: new FormControl(),
      carMark: new FormControl(),
      freightCaseId: new FormControl(),
      buySourceId: new FormControl(),
      freightStateId: new FormControl(),
      cityId: new FormControl(),
      carSizeId: new FormControl(),
      freightPortId: new FormControl(),
      navigationalWayeId: new FormControl(),
      arrivalPortId: new FormControl(),
      carBodyNumber: new FormControl(),
      refranceNumber: new FormControl(),
      createdDate: new FormControl()
    });
    this.filterForm.controls.freightPortId.valueChanges.subscribe(value => { this.loadStates(value) });
    this.form.reset();
    this.filterForm.reset();

  }

  /** Init Form For Search In Sector Sales*/
  initSectorSalesFilterForm() {
    this.filterForm = new FormGroup({
      clinetName: new FormControl(null),
      phoneNumber: new FormControl(null, CustomValidatorsService.phoneNumber),
      refranceNumber: new FormControl(null, CustomValidatorsService.invalidNumber),
      commissionTransfare: new FormControl(null, CustomValidatorsService.invalidNumber),
      createdDate: new FormControl(null),
    });
    this.form.reset();
    this.filterForm.reset();
  }

  /** Init Form For Search In Financial Trnafers*/

  initFinancialTrnafersFilterForm() {
    this.filterForm = new FormGroup({
      userName: new FormControl(null),
      email: new FormControl(null),
      phoneNumber: new FormControl(null, CustomValidatorsService.phoneNumber),
      refranceNumber: new FormControl(null, CustomValidatorsService.invalidNumber),
      commissionTransfare: new FormControl(null, CustomValidatorsService.invalidNumber),
      createdDate: new FormControl(null),
    });
    this.form.reset();
    this.filterForm.reset();
  }

  /**
   * Init Form To Filter In User
   */
  initUsersFilterForm() {
    this.filterForm = new FormGroup({
      userName: new FormControl(null),
      email: new FormControl(null),
      createdDate: new FormControl(null),
      languageCode: new FormControl(null),
      branchIndex: new FormControl(BranchesEnum.Main),
      isDeleted:new FormControl(true)
    });
  }

  /** Init Recover Form */
  initRecoverForm() {
    this.form = new FormGroup({
      _id: new FormControl(null, Validators.required),
      refranceNumber: new FormControl(),
      clinetName: new FormControl(),
      name:new FormControl()
    });
    this.form.disable();
  }

  /**Get Items To Select From Them To Freights Page*/
  getItems(itemName?: string): void {
    let itemsNames = itemName ? [itemName] : [
      "clinets", "buySources", "states", "freightPorts", "arrivalPorts", "navigationalWayes"
    ];
    this.http.getSelectItems<any>(itemsNames).subscribe(res => {
      if (res.isSuccess) {
        itemsNames.forEach(itemName => {
          this[itemName] = [];
          if (itemName == "clinets") {
            this.clinets = [{
              _id: null,
              name: "  "
            } as any, ...res.result[itemName]];
          } else
            if (itemName == "states") {
              //Save All States Becuse Filter In It Affter User Select The Freight Port
              this.allStates = res.result.states;
            }
            else
              this[itemName] = [... this[itemName], ...res.result[itemName]];
        });
      }
      AlertService.showResponseMessage(res);
    }, error => {
      AlertService.canRequestError(error);
    })
  }


  /**
   * Load States By Freight Port Id
   */
  loadStates(freightPortId: string) {
    this.states = [];
    if (freightPortId && this.allStates.length)
      this.states = this.allStates.filter(st => st.freightPortId == freightPortId);
    this.loadStateCities();
  }

  /** Load State Cities */
  loadStateCities(stateId: string = this.filterForm.controls.freightStateId.value): void {
    this.cities = [];
    if (this.states.length && stateId) {
      this.cities = this.states.find(m => m._id == stateId)?.cities || []
    }
  }

}//End Class